              </div>
              <!--  -->
              <div class="ms">
                <span class="ms-t"></span>
                <span>
                  <el-input size="small"
                            v-model.trim="form.leader"
                            placeholder=""></el-input>
                </span>
              </div>
              <!--  -->
              <div class="ms">
                <span class="ms-t"></span>
                <span>
                  <el-input size="small"
                            v-model.trim="form.planNum"
                            placeholder=""></el-input>
                </span>
              </div>
            </div>
          </el-col>
        </el-row>
        <!-- / -->
        <el-row style="margin-bottom: 25px">
          <el-col :span="24">
            <div class="item-put">
              <!--  -->
              <div class="ms"
                   style="width: 100%">
                <span class="ms-t"
                      style="margin-right: 38px">/</span>
                <span style="width: 50%">
                  <el-input size="small"
                            style="width: 100%"
                            v-model.trim="form.purpose"
                            placeholder="/"
                            clearable></el-input>
                </span>
              </div>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row style="margin-bottom: 25px">
          <el-col :span="24">
            <div class="item-put">
              <!--  -->
              <div class="ms"
                   style="width: 100%">
                <span class="ms-t"
                      style="margin-right: 38px">/</span>
                <span style="">
                   <el-date-picker
                      v-model="form.startTime"
                      type="date"
                      size="small"
                      placeholder="">
                    </el-date-picker>
                    <span style="padding:0 10px;"></span>
                    <el-date-picker
                      v-model="form.endTime"
                      type="date"
                      size="small"
                      placeholder="">
                    </el-date-picker>
                  <!-- <el-date-picker
                    v-model="form.time"
                    style="width: 100%"
                    size="small"
                    type="daterange"
                    range-separator=""
                    @change="onChange"
                    start-placeholder=""
                    end-placeholder="">
                  </el-date-picker> -->
                  <!-- <a-config-provider :locale="zhCN">
                    <a-range-picker
                      separator=""
                      @change="onChange"
                      format="YYYY-MM-DD"
                      :value="time"
                      :placeholder="['', '']"
                    />
                  </a-config-provider> -->
                  <!-- <ConfigProvider :locale="zhCN">
                    <DatePicker
                      separator=""
                      @change="onChange"
                      format="YYYY-MM-DD"
                      :value="time"
                      :placeholder="['', '']"
                    />
                  </ConfigProvider> -->

                </span>
              </div>
            </div>
          </el-col>
        </el-row>
        <!--  -->
        <el-row style="margin-bottom: 10px">
          <el-col :span="24">
            <div class="item-put">
              <!--  -->
              <div class="ms"
                   style="width: 100%">
                <span class="ms-t"
                      style="margin-right: 38px"></span>
                <span style="">
                  <el-select
                    v-model="itemMember"
                    size="small"
                    :disabled="(form.affiliation && form.affiliation === 1) ? false :
                               (form.affiliation && form.affiliation === 2) ? true : false "
                               @change="optionChange"
                    multiple
                    style="margin-left: 30px;"
                    placeholder="">
                    <el-option
                      v-for="(item,index) in options"
                      :key="index"
                      :label="item.userName"
                      :value="item.pkId">
                    </el-option>
                  </el-select>

                </span>
              </div>
            </div>
          </el-col>
        </el-row>

         <!-- () -->
        <el-row class="other-message">
          <el-col :span="24">
            <div class="ot-ms">
              <div class="ot-ms-t" ></div>
              <div class="ot-ms-c">
                <el-radio-group v-model="form.projectType" @change="changeRadio">
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="2"></el-radio>
                </el-radio-group>
              </div>
            </div>
          </el-col>
        </el-row>

        <!-- () -->
        <el-row class="other-message">
          <el-col :span="24">
            <div class="ot-ms">
              <div class="ot-ms-t" ></div>
              <div class="ot-ms-c">
                <el-radio-group v-model="form.researchType">
                  <el-radio :label="1"></el-radio>
                  <el-radio :label="2"></el-radio>
                  <el-radio :label="3"></el-radio>
                  <el-radio :label="4"></el-radio>
                  <el-radio :label="5"></el-radio>
                  <el-radio :label="6"></el-radio>
                  <el-radio :label="7"></el-radio>
                </el-radio-group>
              </div>
            </div>
          </el-col>
        </el-row>
      </div>
      <div class="more-message con-content">
        <span class="right-abs pointer"
              @click="isShow = !isShow">
              <el-button type="text">
                
                <i class="el-icon-arrow-up"
                   style="margin-top:8px"
                   :class="{ 'arrowTransform': !isShow, 'arrowTransformReturn': isShow}"></i>
              </el-button>
        </span>
      </div>

      <!--  -->
      <el-collapse-transition>
        <div class="other-message con-content"
             style="margin-top: 25px"
             v-show="isShow">
          <h2 class="title-2"></h2>
          <!--  -->
          <el-row>
            <el-col :span="24">
              <div class="ot-ms">
                <div class="ot-ms-t"></div>
                <div class="ot-ms-c">
                  <el-radio-group v-model="form.financeSource">
                    <el-radio :label="1"></el-radio>
                    <el-radio :label="2"></el-radio>
                    <el-radio :label="3"></el-radio>
                    <el-radio :label="4"></el-radio>
                    <el-radio :label="5"></el-radio>
                  </el-radio-group>
                </div>
              </div>
            </el-col>
          </el-row>

          <!--  -->
          <el-row>
            <el-col :span="24">
              <div class="ot-ms">
                <div class="ot-ms-t"></div>
                <div class="ot-ms-c">
                  <el-input type="textarea"
                            style="width: 60%"
                            size="small"
                            v-model.trim="form.description"
                            placeholder=""
                            clearable></el-input>
                </div>
              </div>
            </el-col>
          </el-row>

          <!--  -->
          <el-row style="margin: 10px 0px 20px">
            <el-col :span="24">
              <div class="ot-ms">
                <div class="ot-ms-t"></div>
                <div class="ot-ms-c ms-f">
                  <div class="ms-f-e">
                    <el-upload class="upload-demo"
                              ref="my-upload"
                              action=""
                              name="files"
                              :limit="5"
                              :http-request="httpRequest"
                              :before-upload="beforeUpload"
                              :on-remove="onRemove"
                              :show-file-list="true"
                              :on-exceed="onExceed"
                              :file-list="form.fileList"
                              multiple>
                      <el-button size="small">
                        <i class="el-icon-upload el-icon--left"></i>
                        
                      </el-button>
                    </el-upload>
                  </div>
                  <div class="ms-f-s">
                    :
                    word/excel/pdf/txt/jpg/png/gif/rar/dcm
                  </div>
                </div>
              </div>
            </el-col>
          </el-row>
        </div>
      </el-collapse-transition>
    </div>
  </div>
</template>

<script>

import { mapGetters, mapMutations } from 'vuex'
// import { DatePicker, ConfigProvider } from 'ant-design-vue'
// import zhCN from 'ant-design-vue/lib/locale-provider/zh_CN'
// import 'ant-design-vue/dist/antd.css'
// import moment from 'moment'
// import 'moment/locale/zh-cn'
import {
  createProjects,
  fileUploading,
  deleteUploadFiles,
  comeUploadFiles,
  correctProject,
  getItemMember,
  getBackItemMember
} from '@/api/projectsMangement'
// import Vue from 'vue'

// import { DatePicker, ConfigProvider } from 'ant-design-vue'
// Vue.component(DatePicker.name, DatePicker)
// Vue.component(ConfigProvider.name, ConfigProvider)

// Vue.use(DatePicker)
// Vue.use(ConfigProvider)
// moment.locale('zh-cn')

export default {
  name: '',
  data () {
    return {
      // zhCN,
      txt: '',
      itemName: '',
      time: [], // 
      itemMember: [],
      form: {
        projectName: '', // 
        leader: '', // 
        planNum: '', // 
        purpose: '', // /
        startTime: '',
        endTime: '',
        userEntities: [], // 
        financeSource: '', // ()
        projectType: '', // 
        researchType: '', // 
        description: '', // 
        fileList: [] // 
      },
      loading: false,
      // fileList: [],
      listObj: null, // 
      isShow: false,
      options: []
    }
  },
  props: {},
  computed: {
    ...mapGetters(['userInfo', 'dataSourceValue'])
  },
  watch: {
    listObj: {
      handler (newName, oldName) {
        if (newName) {
          this.form = newName
        }
      },
      immediate: true,
      deep: true
    }
  },
  components: {},
  created () {
    this.getItemMember() // 
    this.$Storage.sessionRemove('projectId')
    if (this.$route.params?.obj) {
      this.itemName = this.$route.params.obj?.projectName
    } else {
      this.itemName = ''
    }
  },
  mounted () {
    this.listObj = this.$route.params?.obj ?? null
    // console.log(this.listObj)
    if (this.$route.params?.obj) {
      // 
      const { id } = this.$route.params.obj
      this.listObj = this.$route.params.obj
      // this.time = [moment(this.listObj.startTime, 'YYYY-MM-DD'), moment(this.listObj.endTime, 'YYYY-MM-DD')]
      this.comeUploadFiles() // 
      this.getBackItemMember(id) // 
    }
  },
  destroyed () {
  },
  methods: {
    ...mapMutations(['projectsMangement/storeitemdata', 'projectsMangement/projecttype']),
    // 
    // 
    nextStep () {
      if (
        this.form.projectName === '' ||
        this.form.projectType === '' ||
        this.form.researchType === ''
      ) {
        this.$message({
          message: '',
          type: 'warning'
        })
      } else {
        if (this.$route.params.obj) { // 
          this.$set(this.listObj, 'userEntities', [])
          this.itemMember.map(item => {
            this.listObj.userEntities.push({
              userId: item
            })
          })
          this.correctProject()
        } else { // 
          this.itemMember.map(item => {
            this.form.userEntities.push({
              userId: item
            })
          })
          this.putCreateProjects()
        }
      }
    },
    goBack () {
      this.$router.push('/PM/ProjectLists')
    },
    // 
    putCreateProjects () {
      this.txt = '......'
      this.loading = true
      const datas = {
        createBy: this.userInfo?.pkId,
        dataSourceId: this.dataSourceValue?.id
      }
      const data = Object.assign(datas, this.form)
      createProjects(data).then((res) => {
        if (res?.obj) {
          this.$Storage.sessionRemove('projectItemDatas') //
          this['projectsMangement/storeitemdata'](res.obj.id)
          this.$Storage.sessionSet('projectId', res.obj.id) //  id  session
          this.$Storage.sessionSet('projectItemDatas', res.obj) //
          this.$emit('next', 1)
        }
        this.loading = false
      }).catch(() => {
        this.loading = false
      })
    },
    // 
    correctProject () {
      this.txt = '......'
      this.loading = true
      const datas = {
        createBy: this.userInfo?.pkId,
        dataSourceId: this.dataSourceValue?.id
      }
      const data = Object.assign(datas, this.form)
      correctProject(data).then((res) => {
        if (res?.obj) {
          this.$Storage.sessionRemove('projectItemDatas') //
          this['projectsMangement/storeitemdata'](res.obj.id)
          this.$Storage.sessionSet('projectId', res.obj.id) //  id  session
          this.$Storage.sessionSet('projectItemDatas', res.obj) //
          this.$emit('next', 1)
        }
        this.loading = false
      }).catch(() => {
        this.loading = false
      })
    },
    // 
    httpRequest (files) {
      this.txt = '......'
      this.loading = true
      const formData = new FormData()
      formData.append('files', files.file)
      fileUploading(formData).then((res) => {
        if (res?.obj) {
          this.form.fileList.push(res.obj[0])
          const s = this.form.fileList[this.form.fileList.length - 1]
          s.name = s.oldFileName
          s.url = s.filePath
          this.$message({
            message: '~',
            type: 'success'
          })
        }
        this.loading = false
      }).catch(() => {
        this.loading = false
      })
    },
    // 
    submitUpload () {
      const formData = new FormData() //  FormData
      this.fileList.forEach((file) => {
        formData.append('files', file.raw, file.raw.name) // append file.raw fileList file.raw.name
      })
      fileUploading(formData).then((res) => {
        if (res?.obj) {
          this.form.fileList.push(res.obj[0])
          this.$message({
            message: '~',
            type: 'success'
          })
        }
      })
    },
    // 
    beforeUpload (file) {
      const filePng = file.name.lastIndexOf('.png')
      const fileWord1 = file.name.lastIndexOf('.doc')
      const fileWord2 = file.name.lastIndexOf('.docx')
      const fileExcel1 = file.name.lastIndexOf('.xls')
      const fileExcel2 = file.name.lastIndexOf('.xlsx')
      const filePdf = file.name.lastIndexOf('.pdf')
      const fileTxt = file.name.lastIndexOf('.txt')
      const fileJpg = file.name.lastIndexOf('.jpg')
      const fileGif = file.name.lastIndexOf('.gif')
      const fileRar = file.name.lastIndexOf('.rar')
      const fileDcm = file.name.lastIndexOf('.dcm')
      if (
        filePng === -1 &&
        fileWord1 === -1 &&
        fileWord2 === -1 &&
        fileExcel1 === -1 &&
        fileExcel2 === -1 &&
        filePdf === -1 &&
        fileTxt === -1 &&
        fileJpg === -1 &&
        fileGif === -1 &&
        fileRar === -1 &&
        fileDcm === -1
      ) {
        this.$message({
          message: '~',
          type: 'warning'
        })
        return false
      }
    },
    // 
    onRemove (file) {
      // console.log(file)
      const deleteFile = this.form.fileList.filter(
        (item) => file.name === item.oldFileName
      )[0].fileName // 
      const data = {
        fileName: deleteFile,
        id: file.id || ''
      }
      deleteUploadFiles(data).then((res) => {
        if (res) {
          this.$message({
            message: res.msg,
            type: 'success'
          })
          this.form.fileList.map((item, index) => {
            if (file.name === item.oldFileName) {
              this.form.fileList.splice(index, 1)
            }
          })
        }
      })
    },
    // 
    onExceed () {
      this.$message({
        message: '5~',
        type: 'warning'
      })
    },
    // 
    comeUploadFiles () {
      const data = {
        projectId: this.listObj?.id
      }
      comeUploadFiles(data).then((res) => {
        if (res) {
          res.obj.map((item, index) => {
            this.form.fileList.push(item)
            this.form.fileList[index].name = item.oldFileName
            this.form.fileList[index].url = item.filePath
          })
          // console.log(this.form)
        }
      })
    },
    //  
    changeRadio (val) {
      // this['projectsMangement/projecttype'](val)
      // this.$Storage.sessionSet('projectType', val)
      // this.resetSetItem('projectType', val) // 
    },

    // 
    async getItemMember () {
      // const { id } = this.$route.params.obj
      const data = {}
      await getItemMember(data).then(res => {
        if (res?.obj) {
          this.options = res.obj || []
        } else {
          this.options = []
        }
      })
      // await this.getBackItemMember(id)
    },
    // 
    getBackItemMember (id) {
      const data = { id }
      getBackItemMember(data).then(res => {
        // console.log(res?.obj)
        if (res?.obj) {
          this.itemMember = res?.obj || []
        }
      })
    },
    // 
    onChange (date, dateString) {
      // console.log(date, dateString)
      // this.form.startTime = moment(date[0]).format('YYYY-MM-DD')
      // this.form.endTime = moment(date[1]).format('YYYY-MM-DD')
      this.form.startTime = dateString[0]
      this.form.endTime = dateString[1]
      this.time = date
    },
    optionChange (val) {
      // console.log(val)
    }
  }
}
</script>

<style lang="scss" scoped>
@import "~@/styles/mixin.scss";
// @import '~@/styles/antd.css';
.project-create {
  padding-bottom: 50px;
  .right-button {
    top: 9px;
  }
  .create-content {
    padding: 10px 40px;
    background: #ffffff;
    .con-content {
      .title-2 {
        font-size: 16px;
        font-weight: 700;
        color: #999999;
        margin-bottom: 10px;
      }
    }
    .project-message {
      .item-put {
        overflow: hidden;
        .ms {
          float: left;
          margin-right: 30px;
          display: flex;
          align-items: center;
          .ms-t {
            font-size: 14px;
            color: #666666;
          }
        }
      }
    }
    .other-message {
      .ot-ms {
        font-size: 14px;
        margin-top: 10px;
        margin-bottom: 10px;
        .ot-ms-t {
          color: #666666;
          margin-bottom: 15px;
        }
        .ot-ms-c {
          color: #333333;
        }
        .ms-f {
          position: relative;
          .ms-f-e {
            width: 200px;
          }
          .ms-f-s {
            color: #999999;
            font-size: 12px;
            position: absolute;
            top: 5px;
            left: 115px;
          }
        }
      }
    }
    .more-message{
      position: relative;
      .right-abs{
        position: absolute;
        right: 10px;
        top: 0px;
      }
    }
  }
  .arrowTransform {
    transition: 0.2s;
    transform-origin: center;
    transform: rotateZ(180deg);
  }
  .arrowTransformReturn {
    transition: 0.2s;
    transform-origin: center;
    transform: rotateZ(0deg);
  }
}
</style>
